<template>
  <div class="news">
    <sequential-entrance fromBottom>
      <div class="top_news"></div>
    <div class="container">
        <div id="tabs">
      <ul>
        <li :class="showTab ? 'active' : ''" @click="showTab = !showTab">
          <a>Featured News</a>
        </li>
        <li :class="showTab ? '' : 'active'" @click="showTab = !showTab">
          <a>Upcoming News</a>
        </li>
      </ul>
    </div>
      <div class="news_list" v-if="showTab">
        <div class="news_card" v-for="(news, id) in newsList" :key="id">
          <div class="news_img"><img :src="news.image" alt=""></div>
          <div class="news_desc">
            <h3 class="news_head" :head="newsList.head">{{ news.head }}</h3>
            <div class="news_text">{{ news.text }}</div>
            <div class="news_date">{{ news.date }}</div>
          </div>
        </div>
      </div>
      <div class="news_list" v-if="!showTab">
        <div class="news_card" v-for="(news, id) in comingNewsList" :key="id">
          <div class="news_img"><img :src="news.image" alt=""></div>
          <div class="news_desc">
            <h3 class="news_head" :head="newsList.head">{{ news.head }}</h3>
            <div class="news_text">{{ news.text }}</div>
            <div class="news_date">{{ news.date }}</div>
          </div>
        </div>
      </div>
    </div>
    </sequential-entrance>
  </div>
</template>

<script>
// import "bulma/css/bulma.css";
import axios from "axios";
export default {
  name: "News",
  // props: ['id', 'image', 'head', 'text', 'date'],
  data() {
    return {
      newsList: [],
      comingNewsList: [],
      showTab: true
    };
  },
  mounted() {
      const url = "./news.json"
      axios
        .get(url)
        .then((resp) => {
            this.newsList = resp.data;
        })
        .catch((error) => {
          console.log(error);
        });
        const url_2 = "./news_2.json"
      axios
        .get(url_2)
        .then((resp) => {
            this.comingNewsList = resp.data;
        })
        .catch((error) => {
          console.log(error);
        });
  }
};
</script>
